import pic from '../images/HomePic.png';
import About from './About'
import {Link} from 'react-router-dom';
const Home = () => {

    const searchAderess = ()=>{
        let url = `https://www.google.com/maps/search/1235%20Brookdale%20Ave`;
        window.open(url,'_blank');
    }
    return (  
    <>
        <div className = 'flex wrap1'>
            <h1 style = {{paddingTop: '10vh', color: 'purple'}}>At Home Snow Cones</h1>
            <img src= {pic} alt = 'pic'></img>
            <h3 style = {{paddingLeft: '15vw', paddingRight: '15vw', textAlign: 'center'}}>It's summer and it's a hot one, so Om and Luke have started a snow cone buisness! Go ahead and make your way over to <div onClick = {()=>searchAderess()} style = {{color: 'purple',textDecoration: 'underline'}}>1235 Brookdale Ave, Mountain View, CA</div> or go ahead and place an online order <Link to = '/order' style = {{color: 'purple'}}>here</Link>. Feel free to call in at (650)690-0235 for more information.</h3>
        </div>
        <About/>
        
    </>
    );
}
 
export default Home;